import { useContext } from 'react'
import { useNavigate } from 'react-router-dom'
import { Form, Input, Toast } from 'antd-mobile'

import styles from './style.module.scss'
import { context } from '../../hooks/store'
import * as userService from '../../services/user'
import { ReactComponent as IconPwd } from '../../assets/imgs/password.svg'

interface ChangePwdValues {
  oldPwd: string
  newPwd: string
}

export default function ChangePwd() {
  const navigate = useNavigate()
  const { setUser } = useContext(context)
  const [form] = Form.useForm<ChangePwdValues>()

  const changePwd = async () => {
    try {
      const values = await form.validateFields()
      const res = await userService.changePwd(values.oldPwd, values.newPwd)
      if (res.code === 0) {
        Toast.show('修改成功，请重新登录')
        form.resetFields()
        setUser({
          nickname: '',
          account: '',
        })
        navigate('/login')
      } else {
        Toast.show(res.message)
      }
    } catch (error) {
      Toast.show('网络开小差啦')
    }
  }

  return (
    <div className={styles.pwdPage}>
      <div className={styles.title}>
        <IconPwd />
        <span>修改密码</span>
      </div>
      <div className={styles.formWrap}>
        <div className={styles.container}>
          <Form form={form}>
            <Form.Item
              name='oldPwd'
              rules={[
                {
                  required: true,
                  message:
                    '8~16位，需为字母（包含大小写）和数字以及特殊字符 .?!@ 的组合，如 Admin123.',
                },
              ]}
            >
              <Input placeholder='旧密码' type='password' minLength={8} maxLength={16} />
            </Form.Item>
            <Form.Item
              name='newPwd'
              rules={[
                {
                  required: true,
                  message:
                    '8~16位，需为字母（包含大小写）和数字以及特殊字符 .?!@ 的组合，如 Admin123.',
                },
              ]}
            >
              <Input placeholder='新密码' type='password' minLength={8} maxLength={16} />
            </Form.Item>
            <Form.Item>
              <button onClick={changePwd}>提交</button>
            </Form.Item>
          </Form>
        </div>
      </div>
    </div>
  )
}
