export interface ApiResp<T = any> {
  code: number
  message: string
  data: T
}

// 任务类型
export enum TodoType {
  // 未完成
  Task = 'task',
  // 重要
  Important = 'important',
  // 已完成
  Finished = 'finished',
}

// 用户
export interface IUser {
  userId?: string
  // 账号
  account: string
  // 昵称
  nickname: string
  // 加密之后的密码
  password?: string
}

// 任务
export interface ITodo {
  _id: string
  // 内容
  content: string
  // 是否完成
  finished: boolean
  // 是否重要
  important: boolean
}
